body {
  margin: auto;
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 20px;
  white-space: pre-line;
}
a {
  color: teal;
}

button {
  color: darkslateblue;
  background: white;
  border: none;
  font-size: 1.2em;
  padding: 10px;
  margin: 20px 20px;
}
button:hover {
  color: teal;
}
h1 {
  font-size: 1.8em;
  color: black;
}
h4 {
  font-size: 1.5em;
  font-style: italic;
}
h2 {
  font-size: 1.5em;
  color: darkslategrey;
}
img {
  width: 80%;
}
blockquote {
  width: 80%;
}

#loading {
  width: 200px;
  height: 200px;
}

div {
  padding: 5px;
  margin: 10px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#about {
  display: none;
  position: absolute;
  top: 200px;
  left: 20%;
  width: 60%;
  background: white;
  padding: 20px;
  margin: 15px;
  border: darkslateblue;
  border-style: solid;
  border-width: 2px;
}
#heading {
  font-size: 2.5em;
  margin: 10px;
}

@media only screen and (max-width: 600px) {
  body {
    font-size: 14px;
  }
  h1 {
    font-size: 16px;
    color: darkslategray;
  }
  h4 {
    font-size: 18px;
    font-style: italic;
  }
  h2 {
    font-size: 15px;
    color: darkslateblue;
  }
  img {
    width: 80%;
  }
  blockquote {
    width: 90%;
  }
}
